.app {
  box-sizing: border-box;
  background-color: #bbb;
  max-width: 50%;
  min-height: 50vh;
  margin: 0 auto;
  padding: 0 5px;
}

h1 {
  padding-top: 20px;
}

button {
  padding: 5px 10px;
  font-size: 20px;
  border: 1px solid black;
  cursor: pointer;
  transition: .2s;
  outline: none;
  width: 100px;
}

button:hover {
  background-color: black;
  color: white;
}

p {
  padding: 15px;
  background-color: #fff;
  text-align: justify;
  margin-bottom: 10px;
}